import React, { useContext, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link, NavLink } from "react-router-dom";
import { StoreContext } from "../store/StoreProvider";

const MainNav = () => {
  const [bodyFixed, setBodyFixed] = useState(false);
  const { cartStatus } = useContext(StoreContext);

  return (
    <nav class="navbar navbar-expand-lg sticky-top navbar-light bg-light">
      <div className="container fullsize">
        <Link class="navbar-brand" to="/">
          MainShop
        </Link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={() => {
            setBodyFixed(!bodyFixed);
            bodyFixed
              ? (document.body.style.height = "")
              : (document.body.style.height = "100%");
            bodyFixed
              ? (document.body.style.overflow = "")
              : (document.body.style.overflow = "hidden");
            // bodyFixed
            //   ? (document.querySelector(".fullsize").style.height = "")
            //   : (document.querySelector(".fullsize").style.height =
            //       "calc(100vh - 16px)");
          }}
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <NavLink className="nav-link" to="/about">
                O nas
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/contact">
                Kontakt
              </NavLink>
            </li>
            <li className="nav-item dropdown">
              <NavLink
                class="nav-link dropdown-toggle"
                to="#"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Sklep
              </NavLink>
              <div
                class="dropdown-menu"
                aria-labelledby="navbarDropdown d-flex flex-column"
              >
                <Link class="dropdown-item" to="/shop">
                  Wszystkie produkty
                </Link>
                <div class="dropdown-divider"></div>
                <Link class="dropdown-item" to="/shop/mens-clothing/all">
                  Moda męska
                </Link>
                <Link class="dropdown-item" to="/shop/jewelery/all">
                  Biżuteria
                </Link>
                <Link class="dropdown-item" to="/shop/electronics/all">
                  Elektronika
                </Link>
                <Link class="dropdown-item" to="/shop/womens-clothing/all">
                  Moda kobieca
                </Link>
              </div>
            </li>
          </ul>
          <form class="form-inline my-2 my-lg-0">
            <input
              class="form-control mr-sm-2"
              type="search"
              placeholder="Search"
              aria-label="Search"
            />
            <button class="btn btn-outline-success my-2 my-sm-0" type="submit">
              Search
            </button>
          </form>
          <ul class="navbar-nav">
            <li class="nav-item">
              <NavLink className="nav-link" to="/account">
                Twoje konto
              </NavLink>
            </li>
            <li class="nav-item">
              <NavLink className="nav-link" to="/favourites">
                Ulubione
              </NavLink>
            </li>
            <li class="nav-item">
              <NavLink className="nav-link" to="/shopping-cart">
                Koszyk ({cartStatus.length})
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default MainNav;
