import React, { useContext } from "react";
import { StoreContext } from "../../../store/StoreProvider";
import { itemsPerPage } from "../Shop";

const Item = ({
  category,
  activePage,
  isItemInCart,
  setIsItemInCart,
  itemAddedToCart,
  setItemAddedToCart,
}) => {
  const {
    items,
    cartStatus,
    setCartStatus,
    favouritesList,
    setFavouritesList,
  } = useContext(StoreContext);

  const alreadyInCartAlert = (
    <div class="alert alert-danger my-2" role="alert">
      Produkt znajduje się już w koszyku
    </div>
  );

  const addToCartAlert = (
    <div class="alert alert-success" role="alert">
      Produkt dodany do koszyka.
    </div>
  );

  const addToFavourites = (
    <div class="alert alert-success" role="alert">
      Produkt dodany do koszyka.
    </div>
  );

  const errorMsg = (
    <p>
      Błąd podczas pobierania przedmiotów. Prosze odświeżyć stronę bądź
      spróbować później
    </p>
  );

  const handleFavouriteButton = (e) => {};

  const handleAddToCartButton = (e) => {};

  const itemToShow = (title, description, type, price, id, kind) =>
    items ? (
      <div className="col-md-4">
        <div className="card mb-4">
          <img
            src={`/images/${kind}.jpg`}
            alt={title}
            className="card-img-top"
          />
          <div className="card-body">
            <h3 className="card-title mb-0">
              {title}
              {type === "new" && <span className="badge-success">New!</span>}
            </h3>
            <p className="">
              {description.substring(0, description.indexOf(".") + 1)}
            </p>
          </div>
          <div className="card-footer text-center">
            {price.toFixed(2)} $ id {id}
            <div>
              <button
                data-value={id}
                onClick={(e) => {
                  const itemId = e.target.getAttribute("data-value");
                  const selectedItem = items.filter((i) =>
                    itemId == i.id ? i : null
                  );

                  const isAlreadyFavourite = favouritesList.filter((i) => {
                    if (i.id == itemId) return i;
                  });

                  if (isAlreadyFavourite.length > 0) {
                    const itemIndex = favouritesList.findIndex(
                      (i) => id === i.id
                    );
                    let newArray = [...favouritesList];
                    newArray.splice(itemIndex, 1);
                    setFavouritesList(newArray);
                    return;
                  } else {
                    setFavouritesList(favouritesList.concat(selectedItem));
                  }
                }}
              >
                {/* <i class="fa fa-heart">+</i> */}
                {favouritesList.findIndex((i) => id === i.id) === -1
                  ? "dodaj do ulubionych"
                  : "usun z ulubionych"}
              </button>
            </div>
            <div>
              <button
                data-value={id}
                onClick={(e) => {
                  const itemId = e.target.getAttribute("data-value");
                  const selectedItem = items.filter((i) =>
                    itemId == i.id ? i : null
                  );

                  const isAlreadyInCart = cartStatus.filter((i) => {
                    if (i.id == id) return i;
                  });

                  if (isAlreadyInCart.length > 0) {
                    setIsItemInCart(id);
                    // setTimeout(() => {
                    //   setIsItemInCart(0);
                    // }, 3000);
                    return;
                  } else {
                    setItemAddedToCart(id);
                    setCartStatus(cartStatus.concat(selectedItem));
                    console.log("hej", cartStatus, selectedItem);
                  }
                }}
              >
                {/* <i class="fa fa-cart-plus"></i> */}
                dodaj do koszyka
              </button>
              {isItemInCart === id && alreadyInCartAlert}
              {itemAddedToCart === id && addToCartAlert}
              {/* {itemAddedToFavourites === id && addToFavourites} */}
            </div>
          </div>
        </div>
      </div>
    ) : (
      { errorMsg }
    );
  const showItems = () => {
    if (items) {
      const itemsToShow = items
        .filter((i) => {
          if (category === "all" || i.category === category) {
            return i;
          }
        })
        .map((item) => {
          let kind = item.category;
          const { title, description, type, price, id } = item;
          return itemToShow(title, description, type, price, id, kind);
        });
      if (itemsToShow.length < itemsPerPage) {
        return itemsToShow;
      } else if (itemsToShow.length >= itemsPerPage) {
        const smallArray = itemsToShow.filter((i, index) => {
          if (
            index >= itemsPerPage * (activePage - 1) &&
            index < itemsPerPage * activePage
          ) {
            return i;
          }
        });
        return smallArray;
      } else {
        return errorMsg;
      }
    } else {
      return errorMsg;
    }
  };

  return <> {showItems()}</>;
};

export default Item;
