import React, { useState } from "react";
import Item from "./Item";
import Pagination from "./Pagination";

const ItemsShowList = ({ category, itemsArray }) => {
  const [activePage, setActivePage] = useState(1);
  const [isItemInCart, setIsItemInCart] = useState(0);
  const [itemAddedToCart, setItemAddedToCart] = useState(0);
  return (
    <div className="col-md-9">
      <h2 className="mb-4" id="panel">
        Wybierz coś dla siebie{" "}
      </h2>
      <div className="row">
        <Item
          category={category}
          activePage={activePage}
          isItemInCart={isItemInCart}
          setIsItemInCart={setIsItemInCart}
          itemAddedToCart={itemAddedToCart}
          setItemAddedToCart={setItemAddedToCart}
        />
      </div>
      <Pagination
        itemsArray={itemsArray}
        category={category}
        activePage={activePage}
        setActivePage={setActivePage}
      />
    </div>
  );
};

export default ItemsShowList;
