import React from "react";

const Footer = () => {
  return (
    <footer className="bg-secondary text-white pt-4 mt-4">
      <div className="container pb-5">
        <div className="row">
          <div className="col-md-8">
            <h5>MainShop</h5>
            <p className="info">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. At, ut,
              enim dicta est fugit atque quos exercitationem nostrum
              reprehenderit ullam mollitia autem, corrupti libero modi rerum
              eius iure. Repudiandae, in?
            </p>
          </div>
          <div className="col-md-4">
            <form action="">
              <h5>Newsletter</h5>
              <div className="input-group">
                <input
                  type="email"
                  className="form-control"
                  placeholder="e-mail adress"
                />
                <button className="btn btn-primary">Sign in</button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div className="second-footer py-4 d-flex justify-content-center align-items-center ">
        <p className="text-white mb-0">Prawa zastrzeżone @ MainShop</p>
      </div>
    </footer>
  );
};

export default Footer;
