import React, { useContext } from "react";
import { StoreContext } from "../../store/StoreProvider";

const Favourites = () => {
  const { favouritesList, items, cartStatus, setCartStatus } =
    useContext(StoreContext);

  const favouritesToShow = () => {
    return favouritesList.map((item) => {
      return (
        <div className="col-md-4">
          <div className="card mb-4">
            <p>{item.title}</p>
            <p>{item.price.toFixed(2)}</p>
            <button
              data-value={item.id}
              onClick={(e) => {
                const itemId = e.target.getAttribute("data-value");
                const selectedItem = items.filter((i) =>
                  itemId == i.id ? i : null
                );

                const isAlreadyInCart = cartStatus.filter((i) => {
                  if (i.id == item.id) return i;
                });

                if (isAlreadyInCart.length > 0) {
                  return;
                } else {
                  setCartStatus(cartStatus.concat(selectedItem));
                  console.log("hej", cartStatus, selectedItem);
                }
              }}
            >
              {/* <i class="fa fa-cart-plus"></i> */}
              dodaj do koszyka
            </button>
          </div>
        </div>
      );
    });
  };
  return (
    <div className="container">
      <div>Ulubione</div>
      {favouritesList.length <= 0 ? (
        <p>Twoja lista życzeń jest pusta</p>
      ) : (
        <div className="row">{favouritesToShow()}</div>
      )}
    </div>
  );
};

export default Favourites;
